import React from 'react';
import { classdFn as classNames } from 'classd';
import styles from '@styles/components/icon.module.scss';
import * as Icons from 'react-feather';
import ChevronLeft from '@static/icons/chevron-left.inline.svg';
import ChevronRight from '@static/icons/chevron-right.inline.svg';
import ChevronDown from '@static/icons/chevron-down.inline.svg';
import UpRightArrow from '@static/icons/arrow-up-right.inline.svg';
import RightArrow from '@static/icons/arrow-right.inline.svg';
import X from '@static/icons/x.inline.svg';
import LeftRightArrow from '@static/icons/left-right-arrow.inline.svg';
import UpDownArrow from '@static/icons/up-down-arrow.inline.svg';


export default (props) => {
  const { 
    name,
    className,
    small,
    ...rest
  } = props;

  let classes = classNames(
    styles.icon, 
    className,
    styles[name],
    small ? styles.small : null,
  )

  let Icon = () => <></>;

  switch (name) {
    case 'chevronLeft':
      Icon = () => <ChevronLeft className={classes} {...rest} />
      break;
    case 'chevronRight':
      Icon = () => <ChevronRight className={classes} {...rest} />
      break;
    case 'chevronDown':
      Icon = () => <ChevronDown className={classes} {...rest} />
      break;
    case 'linkArrow':
      Icon = () => <UpRightArrow className={classes} {...rest} />
      break;
    case 'rightArrow':
      Icon = () => <RightArrow className={classes} {...rest} />
      break;
    case 'x':
      Icon = () => <X className={classes} {...rest} />
      break;
    case 'leftRightArrow':
      Icon = () => <LeftRightArrow className={classes} {...rest} />
    case 'upDownArrow':
      Icon = () => <UpDownArrow className={classes} {...rest} />
  }

  return (
    <Icon />
  );
}