import React, { useState, useRef, useEffect } from 'react';
import SEO from '@components/SEO';
import Text from '@components/Text';
import Link from '@components/Link';
import Icon from '@components/Icon';
import styles from '@styles/pages/gallery.module.scss';

export default (props) => {

  const { 
    projects, 
    artists, 
    setlist 
  } = props.data;

  let data = [];

  for (let id of setlist) {
    // console.log(id, projects[id])
    data.push({
      id: id,
      ...projects[id],
      artists: [
        { ...artists[projects[id].artists[0]] },
        { ...artists[projects[id].artists[1]] },
      ],
      name: projects[id].work.visual[0].name,
      img: `${props.data.assets.cdn}/works/${projects[id].artists[0]}-1.jpg`
    });
  }

  const scrollRef = useRef();
  const [scrolled, setScrolled] = useState(false);
  const [width, setWidth] = useState(9999);

  const isMobile = (width < 992);

  const transformScroll = (event) => {
    // if (!event.deltaY) {
    //   return;
    // }
    if (width < 992) return;

    if (scrolled === false && event.currentTarget.scrollLeft > 25) {
      setScrolled(true);
    }
  
    event.currentTarget.scrollLeft += event.deltaY + event.deltaX;
    event.preventDefault();
  }

  useEffect(()=>{
    if (scrolled) window.location.hash = '';
  }, [scrolled])

  useEffect(()=>{
    scrollRef.current.addEventListener('wheel', transformScroll);
    return () => scrollRef.current.removeEventListener('wheel', transformScroll);
  }, [width]);

  useEffect(()=>{
    setWidth(window.innerWidth);

    const handleResize = () => {
      setWidth(window.innerWidth);
    }
    
    window.addEventListener('resize', handleResize);
    return () => (window.removeEventListener('resize', handleResize));
  }, []);

  const hiddenScrollNoteStyle = {
    transition: '750ms 1000ms',
    opacity: 0,
  }

  return (
    <>
      <SEO title="Gallery" />
      <div className={styles.wrapper}>
        <div className={styles.scrollNote} style={scrolled ? hiddenScrollNoteStyle : {}}>
          Scroll 
          <Icon className={styles.leftRight} name="leftRightArrow"/> 
          or <Icon className={styles.upDown} name="leftRightArrow" />
        </div>
        <div className={styles.inner} ref={scrollRef}>
          <ul className={styles.list}>
            {data.map((project, i) =>
              <li
                key={project.id}
                id={project.id}
                className={styles.itemWrapper}
                style={!isMobile ? {zIndex: data.length - i} : {}}
              >
                <Link 
                  to={`/gallery/${project.id}`} 
                  className={styles.item}
                  focusClassName={styles.itemFocus}
                  disableDefaultClass
                > 
                  <div className={styles.work}>
                    <div className={styles.overlay}>
                      <Icon className={styles.arrow}name="rightArrow" />  
                    </div>
                    <img src={project.img} />
                  </div>
                  <div className={styles.desc}>
                    <Text h1 className={styles.title}>
                      {project.name} <br/>by
                    </Text>
                    <Text h2 className={styles.names}>
                      <span>
                        {project.artists[0].name}
                        <span style={{color: project.color.fg}}>x</span>
                      </span>
                      <span>{project.artists[1].name}</span>
                    </Text>
                  </div>
                </Link>
              </li>
            )}
          </ul>
        </div>
      </div>
    </>
  );
}